import Vue from 'vue'
import App from './App.vue'
import VueRouter from 'vue-router'
import Home from './components/Home.vue'

import '../public/style.scss' // global style
import '../public/icofont/icofont.min.css' // global font ico


Vue.use(VueRouter)

const routes = [

	{ path: '/', component: Home },
	{ path: '/about', component: () => { return import ('./components/About.vue')} },
	{ path: '/route-1', component: () => { return import ('./components/RouteOne.vue')} },
	{ path: '/route-2', component: () => { return import ('./components/RouteTwo.vue')} },
	{ path: '/route-3', component: () => { return import ('./components/RouteThree.vue')} },
	{ path: '/route-4', component: () => { return import ('./components/RouteFour.vue')} },
	{ path: '*', redirect: '/' }

]

const router = new VueRouter ({
	routes,
	mode: 'history',
	base: process.env.BASE_URL,
})

new Vue({
  render: h => h(App),
  router,
}).$mount('#app')
